.gallery-card {
  width: 250px;
  height: 250px;
  object-fit: cover;
  border-radius: 20px;

  margin: 15px auto;

  &__wrapper {
    // display: grid;
    // grid-template-columns: repeat(3, 1fr);
    // justify-content: center;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    margin-top: 40px;
    margin-bottom: 50px;
  }
}

@media (max-width: $phone) {
  .gallery-card {
    width: 90%;
    height: 260px;

    &__wrapper {
      grid-template-columns: repeat(1, 1fr);
      margin-top: 0;
      margin-bottom: 10px;
    }
  }
}

@media (min-width: $phone) and (max-width: $desktop) {
  .gallery-card {
    &:nth-child(3) {
      display: none;
    }
    &__wrapper {
      grid-template-columns: repeat(2, 1fr);
    }
  }
}
