.our-service-item {
  position: relative;
  width: 100%;
  height: 330px;
  border-radius: 25px;
  background-color: $secondary-black;
  line-height: 1.2;
  padding: 10px;
  transition: 0.3s;

  &.small {
    width: 30%;
  }

  &__wrapper {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 30px;
  }

  &__title {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: 0.3s;
    font-size: 24px;
    text-transform: capitalize;
    opacity: 1;
  }

  &__btn {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    color: $white;
    font-size: 18px;
    border-color: $white;
    text-align: center;
    height: 50px;
    min-width: 200px;
    opacity: 0;
    margin: auto;
  }

  &:hover {
    background-color: $orange;
  }

  &:hover &__title {
    opacity: 0;
  }

  &:hover &__btn {
    opacity: 1;
  }
}

@media (max-width: $phone) {
  .our-service-item {
    width: 100%;

    &__wrapper {
      grid-template-columns: 1fr;
    }
  }
}
